import { Component, OnInit, Output, EventEmitter, AfterViewInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import { authLogout, routeAnimations, AppState, LocalStorageService } from '../../core/core.module';
import {
  actionSettingsChangeTheme
} from '../../core/settings/settings.actions';

import { Store, select } from '@ngrx/store';
import { Observable, timer } from 'rxjs';
import { Router } from '@angular/router';
import { RestApiService } from '../../shared/rest-api.service';
import {take} from 'rxjs/operators';
import * as moment from 'moment';
import { User } from '../../shared/models/user.model';


@Component({
  selector: 'shaale-app-header',
  templateUrl: './app-header.component.html',
  styleUrls: ['./app-header.component.scss'],
  animations: [routeAnimations]
})
export class AppHeaderComponent implements OnInit {

  @ViewChild('stickyMenubar', { static: false }) headerElement: ElementRef;
  @Output() public sidenavToggle = new EventEmitter();


  stickyMenu: boolean = false;
  elementPosition: any;
  notifications: any;
  loggedinUser: User;

  navigation = [
    { link: '/live', label: 'Live' },
    { link: '/learn', label: 'Learn' },
    { link: '/library', label: 'Library' }
  ];

  logo = require('../../../assets/img/logo.png');

  constructor(
    private store: Store<AppState>,
    private storageService: LocalStorageService,
    private router: Router,
    public restApi: RestApiService
  ) {
    this.loggedinUser = JSON.parse(this.restApi.getLoggedinUser());
  }

  ngOnInit() {
    this.getNotificationDetails();
    
    const delay = 10; // every 10 sec
    const count = 15; // process it 5 times
    timer(delay).pipe(
    take(count)).subscribe(x=>{
    // do here whatever you want to do here
    this.getNotificationDetails();

    })
   
  }

  public onToggleSidenav = () => {
    this.sidenavToggle.emit();
  }

  onChangeTheme($event) {
    console.log($event.checked);
    if ($event.checked === true) {
      const theme = 'BLACK-THEME';
      this.store.dispatch(actionSettingsChangeTheme({ theme }))
    } else {
      const theme = 'LIGHT-THEME';
      this.store.dispatch(actionSettingsChangeTheme({ theme }))
    }
  }

  ngAfterViewInit() {

    this.elementPosition = this.headerElement.nativeElement.offsetTop;
    console.log(this.elementPosition);
  }

  @HostListener('window:scroll', ['$event'])
  handleScroll() {
    console.log('hi');
    const windowScroll = window.pageYOffset;
    if (windowScroll >= 1) {
      this.stickyMenu = true;
    } else {
      this.stickyMenu = false;
    }
  }

  logoutFunction() {
    // this.authService.signOut();
    this.store.dispatch(authLogout());
    localStorage.clear();
    this.router.navigateByUrl('');
  }

  getNotificationDetails() {
    this.restApi.getNotificationDetails().subscribe(
      (onSuccess: any) => {
        console.log(onSuccess);
        this.notifications = onSuccess;
        var num = [7, 8, 9];
        onSuccess.notifications.forEach(function (value) {
          console.log(value);
        });
        this.notifications.notifications.forEach((key : any, index: any) => {
          console.log(key, index);
          key.timestamp = moment(key.timestamp).fromNow(); // notification timeline
          console.log(this.notifications);
        })
      },
      (onError: any) => {
        console.log(onError);
      }
    );
  }

  readNotification(notification) {
    console.log(notification);

    const readNotificationUpdate = {
      notifications: [
        {
          type: notification.type,
          id: notification.id
        }
      ]
    }

    this.restApi.updateNotification(readNotificationUpdate).subscribe(
      (onSuccess: any) => {
        console.log(onSuccess);
        if(onSuccess.status) {
          this.getNotificationDetails();
        }
      },
      (onError: any) => {
        console.log(onError);
      }
    );
  }

}
