import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuardService } from '../core/core.module';

import { LandingComponent } from './landing/landing.component';
import { HomeComponent } from './home/home.component'
import { OurStoryComponent } from './our-story/our-story.component';
import { GoLiveComponent } from './go-live/go-live.component';
import { LayoutComponent } from '../layout/layout/layout.component';
import { AppLayoutComponent } from '../layout/app-layout/app-layout.component';
import { LoggedInAuthGuardService } from '../core/auth/logged-in-auth-guard.service';


const routes: Routes = [

  {
    path: '',
    component: LayoutComponent,
    resolve: [LoggedInAuthGuardService],
    children: [
      {
        path: '',
        component: LandingComponent,
      },
    ]
  },
  {
    path: '',
    component: AppLayoutComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'home',
        component: HomeComponent,
      },
      {
        path: 'our-story',
        component: OurStoryComponent,
      },
      {
        path: 'go-live',
        component: GoLiveComponent,
      },
      {
        path: 'abhyas',
        loadChildren: () =>
          import('./abhyas/abhyas.module').then(
            mod => mod.AbhyasModule
          )
      },
      {
        path: 'legal',
        loadChildren: () =>
          import('./legal/legal.module').then(
            mod => mod.LegalModule
          )
      },
      {
        path: 'live',
        loadChildren: () =>
          import('./live/live.module').then(
            mod => mod.LiveModule
          )
      },
      {
        path: 'learn',
        loadChildren: () =>
          import('./learn/learn.module').then(
            mod => mod.LearnModule
          )
      },
      {
        path: 'library',
        loadChildren: () =>
          import('./library/library.module').then(
            mod => mod.LibraryModule
          )
      },
      {
        path: 'connect',
        loadChildren: () =>
          import('./connect/connect.module').then(
            mod => mod.ConnectModule
          )
      },
      {
        path: 'account',
        loadChildren: () =>
          import('./account/account.module').then(
            mod => mod.AccountModule
          )
      },
      {
        path: 'organisation',
        loadChildren: () =>
          import('./organisation/organisation.module').then(
            mod => mod.OrganisationModule
          )
      }
    ]
  },
  {
    path: '**',
    redirectTo: ''
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductsRoutingModule { }
